import "./App.css";

import React from "react";
import { getRndInteger } from "./helper/random";
import words from "./json/words.json";

function useStickyState(defaultValue, key) {
  const [value, setValue] = React.useState(() => {
    const stickyValue = window.localStorage.getItem(key);
    return stickyValue !== null ? JSON.parse(stickyValue) : defaultValue;
  });
  React.useEffect(() => {
    window.localStorage.setItem(key, JSON.stringify(value));
  }, [key, value]);
  return [value, setValue];
}

function App() {
  const dateNow = new Date().getDate();
  const [date, setDate] = useStickyState(dateNow, "date");
  const [category] = useStickyState("technology", "category");
  const wordsList = words[category];
  const [word, setWord] = useStickyState(wordsList[getRndInteger(0, wordsList.length - 1)], "word");
  if (dateNow !== Number(date)) {
    setDate(new Date().getDate());
    setWord(wordsList[getRndInteger(0, wordsList.length - 1)]);
  }

  const handleChangeSelect = () => {};

  return (
    <div style={{ margin: "1rem" }}>
      <div id='categorySelectMenu'>
        <span>
          <input
            type='checkbox'
            id='vehicle1'
            name='technology'
            value='technology'
            checked
            onChange={handleChangeSelect}
          />
          <label htmlFor='technology'>Teknoloji</label>
        </span>
        <span>
          <input
            type='checkbox'
            id='inspiration'
            name='inspiration'
            value='inspiration'
            checked
            onChange={handleChangeSelect}
          />
          <label htmlFor='inspiration'>Ä°lham</label>
        </span>
        <span>
          <input
            type='checkbox'
            id='vehicle3'
            name='motivation'
            value='motivation'
            checked
            onChange={handleChangeSelect}
          />
          <label htmlFor='motivation'>Motivasyon</label>
        </span>
      </div>
      <h1 id='text' style={{ margin: "0" }}>
        {word.text}
      </h1>
      <p id='author' style={{ textAlign: "right", fontSize: "0.8rem", margin: "0" }}>
        - {word.author}
      </p>
    </div>
  );
}

export default App;
